import React from 'react';

  // eslint-disable-next-line
import PropTypes from 'prop-types';

const Card =({property})=>{
    const{index, head, description,picture} = property;
    return(
        <div  id={`card-${index}`} className = 'card'>
            <h1 className='head'>{head}</h1>
            <p className="description">{description}</p>
            <img source ={null} alt='slides' className='emote' />
           <div className ='ewrap'> <img className="emoji" src={picture} alt="emoji"/></div>
        </div>
    )
}
export default Card